<template>
  <div class="comment-item-wrapper">
    <el-avatar
      :src="comment.author?.avatarUrl"
      :size="avatarSize"
      :alt="`${comment.author?.username || 'ç”¨æˆ·'}çš„å¤´åƒ`"
    />
    <div class="comment-content">
      <div class="comment-header">
        <span class="comment-author">{{ comment.author?.username }}</span>
        <span class="comment-time">{{ formatTime(comment.createdAt) }}</span>
      </div>
      <div class="comment-text">
        <span v-if="comment.replyToUser" class="reply-to">
          @{{ comment.replyToUser.username }}
        </span>
        {{ comment.content }}
      </div>
      <div class="comment-actions">
        <el-button text size="small" @click="$emit('reply', comment)">å›å¤</el-button>
        <el-button 
          v-if="canDelete" 
          text 
          size="small" 
          type="danger"
          @click="$emit('delete', comment)"
        >åˆ é™¤</el-button>
        <el-button 
          v-if="canReport" 
          text 
          size="small" 
          type="warning"
          @click="$emit('report', comment)"
        >ä¸¾æŠ¥</el-button>
      </div>
      <!-- å›å¤è¾“å…¥æ¡† -->
      <div v-if="showReplyInput" class="reply-input-container">
        <el-input
          v-model="replyText"
          type="textarea"
          placeholder="å†™ä¸‹ä½ çš„å›å¤..."
          rows="2"
          class="reply-textarea"
        />
        <div class="reply-input-actions">
          <el-button size="small" @click="cancelReply">å–æ¶ˆ</el-button>
          <el-button type="primary" size="small" @click="submitReply" :loading="replying">
            å‘å¸ƒ
          </el-button>
        </div>
      </div>
      <!-- å­è¯„è®ºæ˜¾ç¤ºé€»è¾‘ -->
      <!-- level 0: ä¸€çº§è¯„è®ºçš„å­è¯„è®ºï¼ˆäºŒçº§è¯„è®ºï¼‰æœ‰ç¼©è¿› -->
      <!-- level >= 1: äºŒçº§åŠä»¥ä¸‹çš„å­è¯„è®ºæ— ç¼©è¿›ï¼Œéƒ½åœ¨åŒä¸€ç«–åˆ— -->
      <div v-if="comment.replies?.length" :class="['reply-list', { 'no-indent': level >= 1 }]">
        <!-- å¦‚æœå½“å‰å±‚çº§å°äº1ï¼Œç›´æ¥æ˜¾ç¤ºå­è¯„è®ºï¼ˆä¸€çº§è¯„è®ºæ˜¾ç¤ºäºŒçº§è¯„è®ºï¼‰ -->
        <template v-if="level < 1">
          <CommentItem
            v-for="reply in comment.replies"
            :key="reply.id"
            :comment="reply"
            :level="level + 1"
            :avatar-size="24"
            :can-delete="canDeleteComment(reply)"
            :can-report="canReportComment(reply)"
            :format-time="formatTime"
            :can-delete-comment="canDeleteComment"
            :can-report-comment="canReportComment"
            :reply-target-id="replyTargetId"
            @reply="handleReply"
            @delete="$emit('delete', $event)"
            @report="$emit('report', $event)"
            @submit-reply="$emit('submit-reply', $event)"
            @cancel-reply="$emit('cancel-reply')"
          />
        </template>
        <!-- å¦‚æœå½“å‰å±‚çº§>=1ï¼Œæ˜¾ç¤ºæŠ˜å /å±•å¼€æŒ‰é’®ï¼ˆäºŒçº§è¯„è®ºçš„å­è¯„è®ºéœ€è¦æŠ˜å ï¼‰ -->
        <template v-else>
          <div class="collapse-control">
            <el-button 
              text 
              size="small" 
              type="primary"
              @click="isExpanded = !isExpanded"
            >
              <el-icon><ArrowDown v-if="!isExpanded" /><ArrowUp v-else /></el-icon>
              {{ isExpanded ? 'æ”¶èµ·' : `å±•å¼€ ${getTotalRepliesCount(comment)} æ¡å›å¤` }}
            </el-button>
          </div>
          <div v-if="isExpanded" class="expanded-replies">
            <CommentItem
              v-for="reply in comment.replies"
              :key="reply.id"
              :comment="reply"
              :level="level + 1"
              :avatar-size="24"
              :can-delete="canDeleteComment(reply)"
              :can-report="canReportComment(reply)"
              :format-time="formatTime"
              :can-delete-comment="canDeleteComment"
              :can-report-comment="canReportComment"
              :reply-target-id="replyTargetId"
              @reply="handleReply"
              @delete="$emit('delete', $event)"
              @report="$emit('report', $event)"
              @submit-reply="$emit('submit-reply', $event)"
              @cancel-reply="$emit('cancel-reply')"
            />
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed } from 'vue'
import { ArrowDown, ArrowUp } from '@element-plus/icons-vue'
// é€’å½’ç»„ä»¶éœ€è¦æ˜¾å¼å¯¼å…¥è‡ªèº«
import CommentItem from './CommentItem.vue'

const props = defineProps({
  comment: {
    type: Object,
    required: true
  },
  level: {
    type: Number,
    default: 0
  },
  avatarSize: {
    type: Number,
    default: 32
  },
  canDelete: {
    type: Boolean,
    default: false
  },
  canReport: {
    type: Boolean,
    default: false
  },
  formatTime: {
    type: Function,
    required: true
  },
  canDeleteComment: {
    type: Function,
    required: true
  },
  canReportComment: {
    type: Function,
    required: true
  },
  replyTargetId: {
    type: Number,
    default: null
  }
})

const emit = defineEmits(['reply', 'delete', 'report', 'submit-reply', 'cancel-reply'])

// æ§åˆ¶æŠ˜å /å±•å¼€çŠ¶æ€
const isExpanded = ref(false)

// æ§åˆ¶å›å¤è¾“å…¥æ¡†æ˜¾ç¤º
const showReplyInput = computed(() => props.replyTargetId === props.comment.id)
const replyText = ref('')
const replying = ref(false)

// å¤„ç†å›å¤æŒ‰é’®ç‚¹å‡»
const handleReply = (comment) => {
  emit('reply', comment)
}

// æäº¤å›å¤
const submitReply = async () => {
  if (!replyText.value.trim()) {
    return
  }
  replying.value = true
  try {
    emit('submit-reply', {
      comment: props.comment,
      content: replyText.value
    })
    replyText.value = ''
  } finally {
    replying.value = false
  }
}

// å–æ¶ˆå›å¤
const cancelReply = () => {
  replyText.value = ''
  emit('cancel-reply')
}

// é€’å½’è®¡ç®—æ€»å›å¤æ•°ï¼ˆåŒ…æ‹¬æ‰€æœ‰å±‚çº§çš„å­è¯„è®ºï¼‰
const getTotalRepliesCount = (comment) => {
  if (!comment.replies || comment.replies.length === 0) {
    return 0
  }
  let count = comment.replies.length
  comment.replies.forEach(reply => {
    count += getTotalRepliesCount(reply)
  })
  return count
}
</script>

