<template>
  <el-dialog
    v-model="visible"
    title="ä¸¾æŠ¥å†…å®¹"
    width="480px"
    :close-on-click-modal="false"
    @close="handleClose"
  >
    <div class="report-dialog">
      <div class="report-tip">
        <el-icon><WarningFilled /></el-icon>
        <span>è¯·é€‰æ‹©ä¸¾æŠ¥åŸå› ï¼Œæˆ‘ä»¬ä¼šå°½å¿«å¤„ç†æ‚¨çš„ä¸¾æŠ¥</span>
      </div>
      
      <el-form
        ref="formRef"
        :model="form"
        :rules="rules"
        label-position="top"
      >
        <el-form-item label="ä¸¾æŠ¥åŸå› " prop="reason">
          <el-radio-group v-model="form.reason" class="reason-group">
            <el-radio
              v-for="item in REPORT_REASONS"
              :key="item.value"
              :value="item.value"
              border
            >
              {{ item.label }}
            </el-radio>
          </el-radio-group>
        </el-form-item>
        
        <el-form-item label="è¯¦ç»†è¯´æ˜ï¼ˆå¯é€‰ï¼‰" prop="description">
          <el-input
            v-model="form.description"
            type="textarea"
            :rows="3"
            placeholder="è¯·è¡¥å……è¯´æ˜ä¸¾æŠ¥åŸå› ï¼Œå¸®åŠ©æˆ‘ä»¬æ›´å¿«å¤„ç†..."
            maxlength="500"
            show-word-limit
          />
        </el-form-item>
      </el-form>
    </div>
    
    <template #footer>
      <div class="dialog-footer">
        <el-button @click="handleClose">å–æ¶ˆ</el-button>
        <el-button type="danger" :loading="submitting" @click="handleSubmit">
          æäº¤ä¸¾æŠ¥
        </el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script setup>
import { ref, reactive, watch } from 'vue'
import { ElMessage } from 'element-plus'
import { WarningFilled } from '@element-plus/icons-vue'
import { reportApi, REPORT_REASONS } from '@/api/report'

const props = defineProps({
  modelValue: {
    type: Boolean,
    default: false
  },
  contentType: {
    type: String,
    required: true
  },
  contentId: {
    type: [Number, String],
    required: true
  }
})

const emit = defineEmits(['update:modelValue', 'success'])

const visible = ref(false)
const submitting = ref(false)
const formRef = ref(null)

const form = reactive({
  reason: '',
  description: ''
})

const rules = {
  reason: [
    { required: true, message: 'è¯·é€‰æ‹©ä¸¾æŠ¥åŸå› ', trigger: 'change' }
  ]
}

watch(() => props.modelValue, (val) => {
  visible.value = val
  if (val) {
    // é‡ç½®è¡¨å•
    form.reason = ''
    form.description = ''
  }
})

watch(visible, (val) => {
  emit('update:modelValue', val)
})

const handleClose = () => {
  visible.value = false
}

const handleSubmit = async () => {
  if (!formRef.value) return
  
  try {
    await formRef.value.validate()
  } catch {
    return
  }
  
  submitting.value = true
  try {
    await reportApi.submitReport({
      contentType: props.contentType,
      contentId: Number(props.contentId),
      reason: form.reason,
      description: form.description || null
    })
    ElMessage.success('ä¸¾æŠ¥å·²æäº¤ï¼Œæˆ‘ä»¬ä¼šå°½å¿«å¤„ç†')
    emit('success')
    handleClose()
  } catch (error) {
    // é”™è¯¯å·²åœ¨æ‹¦æˆªå™¨ä¸­å¤„ç†
    console.error('æäº¤ä¸¾æŠ¥å¤±è´¥:', error)
  } finally {
    submitting.value = false
  }
}
</script>

